<template>
  <b-row style="text-align:center" class="m-0">
    <b-col cols="12">
      <b-row style="text-align:center">
        <b-col cols="4">
          <h1 class="mt-5">{{date}}</h1>
          <vue-clock />
        </b-col>
        <b-col cols="4" class="bg-dark p-3">
          <div v-if="isCameraOpen" class="camera-box">

            <video v-show="!isPhotoTaken" ref="camera"  autoplay width="100%"></video>

            <canvas v-show="isPhotoTaken" id="photoTaken" ref="canvas" :width="650" :height="537.5"></canvas>
          </div>
        </b-col>
        <b-col cols="4">
          <b-form @submit.prevent="takePhoto">
            <h4 class="mt-2">Please scan your ID</h4>

            <b-form-input
                    v-model="form.id_number"
                    type="password"
                    id="id_number"
                    autofocus
                    required
                    autocomplete="off"
            ></b-form-input>
          </b-form>
          <div class="text-left mt-2" v-if="typeof settings.mode_description!='undefined'">
            <span class="d-block p-1">Location : <strong>{{settings.server_project_location_structure_item.location}}</strong></span>
            <span class="d-block bg-dark text-white p-1">Structure : <strong>{{settings.server_project_location_structure_item.structure}}</strong></span>
            <span class="d-block p-1">Item : <strong>{{settings.server_project_location_structure_item.item}}</strong></span>
            <span class="d-block p-1 bg-warning h2 text-center cursor-pointer" @click="changeSettings()"><strong>{{settings.mode_description}}</strong></span>
          </div>
        </b-col>
      </b-row>
    </b-col>
    <b-col>
      <!--<img :src="captured_image" >-->
      <server-side-paginated-table
              :fields="table_settings.fields"
              :api_url="table_settings.api_url"
              :delete_url="table_settings.delete_url"
              ref="displayTable"
              :top_right_btn_redirect="false"
      />
    </b-col>

    <b-modal
            v-model="settings_modal"
            centered
            size="md"
            body-class="p-0"
            :hide-footer="true"
            :hide-header="true"
    >
      <div class="card mb-0">
        <div
                class="card-header bg-dark text-white d-flex align-items-center justify-content-between"
        >
          <h6 class="mb-0">
            Change Settings
          </h6>
        </div>
        <div class="card-body position-relative">
          <div class="bg-light mb-4 cursor-pointer" @click="changeProject()">
            <span class="d-block p-1 text-dark">Location : <strong>{{settings_form.server_project_location_structure_item.location}}</strong></span>
            <span class="d-block bg-dark text-white p-1">Structure : <strong>{{settings_form.server_project_location_structure_item.structure}}</strong></span>
            <span class="d-block p-1 text-dark">Item : <strong>{{settings_form.server_project_location_structure_item.item}}</strong></span>
          </div>

          <b-form-group label="Mode:" description="This field is required">
            <select class="form-control form-control-sm" v-model="settings_form.mode" required>
              <option value="1">AM IN</option>
              <option value="2">AM OUT</option>
              <option value="3">PM IN</option>
              <option value="4">PM OUT</option>
              <option value="5">OT IN</option>
              <option value="6">OT OUT</option>
            </select>
          </b-form-group>

          <b-form-group label="Password:" description="This field is required">
            <b-form-input v-model="settings_form.password" type="password" required size="sm"></b-form-input>
          </b-form-group>

          <b-button size="sm" block variant="success" @click="saveSettings"><font-awesome-icon icon="save" /> Save Settings</b-button>

        </div>
      </div>
    </b-modal>


    <b-modal
            v-model="projects_modal"
            centered
            size="xl"
            body-class="p-0"
            :hide-footer="true"
            :hide-header="true"
    >
      <div class="card mb-0">
        <div
                class="card-header bg-dark text-white d-flex align-items-center justify-content-between"
        >
          <h6 class="mb-0">
            Select Project
          </h6>
        </div>
        <div class="card-body position-relative">
          <b-input-group prepend="Search">
            <b-form-input v-model="project_search" placeholder="Type your keyword here..." autocomplete="off" debounce="500"></b-form-input>
          </b-input-group>
          <table class="table table-bordered mt-3 ta">
            <thead>
            <tr>
              <th>Location</th>
              <th>Structure</th>
              <th>Item</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="project_item in project_items" class="cursor-pointer" @dblclick="selectProject(project_item)">
              <td>{{project_item.location}}</td>
              <td>{{project_item.structure}}</td>
              <td>{{project_item.item}}</td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </b-modal>
  </b-row>

</template>

<script>
    import {client_details,assets_path} from '@/config';
    import Loading from '@/views/widgets/Loading.vue';
    import VueClock from '@dangvanthanh/vue-clock';
    import SwalMixin from '@/views/mixins/Swal.js'
    import ServerSidePaginatedTable from './ServerSidePaginatedTable.vue';
    export default {
        name: 'Login',
        mixins:[SwalMixin],
        components: {Loading,VueClock,ServerSidePaginatedTable},
        data(){
            return{
              table_settings:{
                api_url:'logs/recent',
                delete_url:'roles/delete',
                fields:[
                  {key: 'id_number', label:'ID Number'},
                  {key: 'am_in', label:'AM IN'},
                  {key: 'am_out', label:'AM OUT'},
                  {key: 'pm_in', label:'PM IN'},
                  {key: 'pm_out', label:'PM OUT'},
                  {key: 'ot_in', label:'OT IN'},
                  {key: 'ot_out', label:'OT OUT'},
                ]
              },
              form:{
                id_number:'',
                image:''
              },
              settings_form:{
                server_project_location_structure_item_id:'',
                mode:1,
                server_project_location_structure_item:{},
                password:''
              },
              date:'',
              isCameraOpen: false,
              isPhotoTaken: false,
              isShotPhoto: false,
              isLoading: false,
              link: '#',
              captured_image:'',
              settings:{},
              settings_modal:false,
              projects_modal:false,
              project_search:'',
              project_items:[]
            }
        },
      watch:{
        project_search:{
          immediate: true,
          deep:true,
          handler(){

            this.axios.get('items/search',{params:{search:this.project_search}}).then(response => {
              this.project_items=response.data;
            }).catch(error => console.log(error));

          }
        },
      },
        created() {
          this.toggleCamera();
          let today = new Date();
          this.date = today.toDateString();
          this.getSettings();
        },
        methods:{
          toggleCamera() {
            if(this.isCameraOpen) {
              this.isCameraOpen = false;
              this.isPhotoTaken = false;
              this.isShotPhoto = false;
              this.stopCameraStream();
            } else {
              this.isCameraOpen = true;
              this.createCameraElement();
            }
          },

          createCameraElement() {
            this.isLoading = true;

            const constraints = (window.constraints = {
              audio: false,
              video: true
            });


            navigator.mediaDevices
                    .getUserMedia(constraints)
                    .then(stream => {
                      this.isLoading = false;
                      this.$refs.camera.srcObject = stream;
                    })
                    .catch(error => {
                      this.isLoading = false;
                      alert("May the browser didn't support or there is some errors.");
                    });
          },

          stopCameraStream() {
            let tracks = this.$refs.camera.srcObject.getTracks();

            tracks.forEach(track => {
              track.stop();
            });
          },

          takePhoto() {
            let today = new Date();
            const context = this.$refs.canvas.getContext('2d');
            context.fillStyle = "yellow";
            context.drawImage(this.$refs.camera, 0, 0, 650, 537.5);
            context.fillText(today, 10, 520);

            //const download = document.getElementById("downloadPhoto");
            const canvas = document.getElementById("photoTaken").toDataURL("image/jpeg");
            this.captured_image = canvas;
            this.form.image = canvas;
            this.axios.post('logs/save',this.form).then(response => {
              this.$refs.displayTable.getAll();
              this.swalTransactionCompleted();
              this.form.id_number='';


            }).catch(error => {
              this.swal_transaction_error_data.text = error.response.data;
              this.swalRequestError();
              this.form.id_number='';
              //this.is_saving=false;
            });
          },

          downloadImage() {
            const download = document.getElementById("downloadPhoto");
            const canvas = document.getElementById("photoTaken").toDataURL("image/jpeg")
                    .replace("image/jpeg", "image/octet-stream");
            download.setAttribute("href", canvas);
          },
          getSettings(){
            this.axios.get('settings/get').then(response => {
              this.settings=response.data;
            }).catch(error => console.log(error));
          },
          changeSettings(){
            this.settings_modal=true;
            let settings_form = {
                      date:this.settings.date,
                      server_project_location_structure_item_id:this.settings.server_project_location_structure_item_id,
                      mode:this.settings.mode,
                      server_project_location_structure_item:this.settings.server_project_location_structure_item
                };
            this.settings_form = settings_form;

          },
          changeProject(){
            this.projects_modal=true;
          },
          selectProject(item){
            this.settings_form.server_project_location_structure_item_id = item.server_project_location_structure_item_id;
            this.settings_form.server_project_location_structure_item = item;
            this.projects_modal=false;
          },
          saveSettings(){
            this.axios.post('settings/save',this.settings_form).then(response => {
              this.swalTransactionCompleted();
              this.settings_modal=false;
              this.getSettings();
            }).catch(error => {
              this.swal_transaction_error_data.text = error.response.data;
              this.swalRequestError();
              //this.is_saving=false;
            });
          },
        }
    }
</script>
<style>

</style>
